<template>
 <div>
   <div>49</div>
  <button style="line-height: 1.5rem;" class="image-item space" @click="getImage()"> </button>
  <button style="line-height: 1.5rem;" class="image-item space" @click="getPhoto()"> </button>
  <button style="line-height: 1.5rem;" class="image-item space" @click="getVideo()"> </button>
  <div style="display:flex;justify-content: space-between;flex-wrap: wrap;width:325px;margin:0 auto;margin-top:30px;">
    <div style="width:150px;height:150px;border:1px solid red;"><img id="img1" src="" style="width:100%;height:100%" alt=""></div>
    <div style="width:150px;height:150px;border:1px solid #abc;"><img id="img2" src="" style="width:100%;height:100%" alt=""></div>
    <div class="photo" style="width:150px;height:150px;border:1px solid #000;"></div>
  </div>
 </div>
 
</template>
<script type="text/ecmascript-6">
  export default {
    name: "cameras-and-albums",
   data(){
    return{
     imgList: [],
     datas: new FormData(),
     files:0,
     size:0
    }
   },
   methods:{
    //(1)
    getImage() { 
      let _this = this     
      let cmr = plus.camera.getCamera();    
      cmr.captureImage(function(p) {    
        plus.io.resolveLocalFileSystemURL(p, function(entry) {    
            _this.compressImage(entry.toLocalURL(),entry.name);    
        }, function(e) {    
            plus.nativeUI.toast("" + e.message);    
        });    
      }, function(e) {    
      }, {    
        filter: 'image'   
      });    
    },
    // 
    compressImage(url,filename){   
      let _this = this
      let name="_doc/upload/"+filename;  
      plus.zip.compressImage({    
        src:url,//src: (String  )    
        dst:name,//    
        quality:90,//quality: (Number  ).1-100    
        overwrite:true,//overwrite: (Boolean  )
        width:'250',
        height:'320'
          
      },    
      function(zip) {  
        //  
        _this.showPics(zip.target,name); 
      },function(error) {    
        plus.nativeUI.toast("");    
      });    
    },
    // 
    showPics(url,name){ 
      $("#img1").removeAttr("src");
      //   
      plus.io.resolveLocalFileSystemURL(url,function(entry){  
        entry.file( function(file){  
          let fileReader = new plus.io.FileReader();  
          fileReader.readAsDataURL(file);  
          fileReader.onloadend = function(e) {  
            let picUrl = e.target.result.toString();  
            let img1 = $("#img1").attr("src");//
            if(img1 == "" || img1 == undefined){    
              $("#img1").attr("src",picUrl);//base64img
            }  
          }  
        });  
      });   
    },

    // (2) 
    getVideo(){
      let _this = this
      let cmr = plus.camera.getCamera();
      cmr.startVideoCapture(function(p){
        plus.io.resolveLocalFileSystemURL(p, function(entry){
          entry.file( function(file){
              let fileReader = new plus.io.FileReader();
              _this.showVideo(file);//
          } );
        }, function(e){
          alert(''+e.message);
        } );
      }, function(e){
      }, {filename:'_doc/camera/',index:1});
    },
    // 
    showVideo(file){
      $("#video0").remove();//video
      let fileSize =(file.size) / (1024*1024);//M
      fileSize = fileSize.toFixed(1);//
      if(fileSize > 30){
        alert('30M');    
      }else{        
        let vde = '<video style="height:150px; width:150px; object-fit:fill;border:1px solid #000;" id="video0" autoplay="autoplay" x5-playsinline="" playsinline="" webkit-playsinline="" loop="loop"></video>';                    
        $(".photo").append(vde);        
        let reader = new plus.io.FileReader();
        reader.readAsDataURL(file);//  
        reader.onload = function(e) {  
          $("#video0").attr("src", e.target.result);//base64
        };        
      }        
    },

    // (3) 
    getPhoto(){
      let _this = this
        plus.gallery.pick(function(path){
          let name = path.substring(path.lastIndexOf("/")+1); 
          _this.compressPhoto(path,name);//
        }, function(e){
        }, {filter:'image'});
    },
    // 
    compressPhoto(url,filename){ 
      let _this = this
        let name="_doc/upload/"+filename;  
        plus.zip.compressImage({    
                src:url,//src: (String  )    
                dst:name,//    
                quality:90,//quality: (Number  ).1-100    
                overwrite:true,//overwrite: (Boolean  )
                width:'250',
                height:'320'                    
            },    
            function(zip) {  
                //  
                _this.showPhoto(zip.target,name); 
            },function(error) {    
                plus.nativeUI.toast("");    
        });    
    },
    //  
    showPhoto(url,name){ 
      $("#img2").removeAttr("src");
      //   
        plus.io.resolveLocalFileSystemURL(url,function(entry){  
            entry.file( function(file){  
                let fileReader = new plus.io.FileReader();  
                fileReader.readAsDataURL(file);  
                fileReader.onloadend = function(e) {  
                      let picUrl = e.target.result.toString();  
                      let img2 = $("#img2").attr("src");//
                      if(img2 =="" || img2 == undefined){    
                        $("#img2").attr("src",picUrl);//base64img
                      }  
                }  
            });  
        });   
    },
    
    fileChange(el) {
     this.files=$("#upload_file").get(0).files;
     console.log(this.files.length);
     for(let i=0;i<this.files.length;i++){
      this.datas.append("file",this.files[i]);
     }
     this.show1=false;
     console.log(typeof this.files);
     console.log(this.files);
     if (!el.target.files[0].size) return;
     this.fileList(el.target);
     el.target.value = ''
    },
    fileList(fileList) {
     let files = fileList.files;
     for (let i = 0; i < files.length; i++) {
      //
      if (files[i].type != '') {
       this.fileAdd(files[i]);
      } else {
       //
       this.folders(fileList.items[i]);
      }
     }
    },
    //
    folders(files) {
     let _this = this;
     //file
     if (files.kind) {
      files = files.webkitGetAsEntry();
     }
     files.createReader().readEntries(function (file) {
      for (let i = 0; i < file.length; i++) {
       if (file[i].isFile) {
        _this.foldersAdd(file[i]);
       } else {
        _this.folders(file[i]);
       }
      }
     })
    },
    fileAdd(file) {
     //
     this.size = this.size + file.size;
     //
     if (file.type.indexOf('image') == -1) {
      file.src = 'wenjian.png';
      this.imgList.push({
       file
      });
     } else {
      let reader = new FileReader();
      reader.vue = this;
      reader.readAsDataURL(file);
      reader.onload = function () {
       file.src = this.result;
       this.vue.imgList.push({
        file
       });
      }
     }
    },
    fileDel(index) {
     this.size = this.size - this.imgList[index].file.size;//
     this.imgList.splice(index, 1);
    },
    bytesToSize(bytes) {
     if (bytes === 0){
      return '0 B';
     }
     let k = 1000, // or 1024
      sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
      i = Math.floor(Math.log(bytes) / Math.log(k));
     return (bytes / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i];
    },
    dragenter(el) {
     el.stopPropagation();
     el.preventDefault();
    },
    dragover(el) {
     el.stopPropagation();
     el.preventDefault();
    },
    drop(el) {
     el.stopPropagation();
     el.preventDefault();
     this.fileList(el.dataTransfer);
    },
    shows(et,tx){
     this.strut=et;
     this.txt=tx;
    },
    handleClick(){
     this.$store.commit('add')
    },
   },
  }
</script>

<style>
div{
  margin-bottom: 20px;
  
}
</style>








